import React,{Component} from 'react'

import Header from '../../containers/Header/index'
import images from '../../images'
import './styles.css'

export default class About extends Component {

    render(){

        return (
            <div className="about">
                <Header/>
                <section>
                    <h1>О НАС</h1>
                    <div className="wrapper">
                        <div id="column-1">
                            <p></p>
                            <img src={images[""]} alt=""/>
                        </div>
                        <div id="column-2">
                            <img src={images[""]} alt=""/>
                        </div>
                        <div id="column-3">
                            <img src={images["swiss.png"]} alt=""/>
                        </div>
                    </div>
                </section>
            </div>
        )
    }

}



