import React, {Component} from 'react'
import { connect } from 'react-redux'
import {NavLink} from 'react-router-dom'

import './styles.css'

class Navigation extends Component {

    doRightButton(){

        let button = document.querySelector('header').getElementsByTagName('button')[0];


        this.props.changeClass(button);
    }

    render(){
        return (

            <ul className='nav'>

                {
                    this.props.navigation.location.map((navItem,index)=>{

                        return (

                            <li key={index}>

                                <NavLink className={this.props.navigation.classes.classLink}
                                         exact to={navItem.link}
                                         onClick={this.doRightButton.bind(this)}>

                                    {navItem.name.toUpperCase()}

                                </NavLink>

                            </li>

                        )

                    })
                }
            </ul>
        )

    }

}

export default connect(
    state=>({

        navigation: state.navigation,
        classButton: state.navigation.classes.classButton,
        pathname: state.navigation.pathname

    }),
dispatch=>({

    changeClass: (button)=>{

        dispatch({type:"CHANGE_MENU_BUTTON_CLASS", payload:button})
    }
})


)(Navigation)


