import React from 'react'
import { createStore, applyMiddleware, compose } from 'redux'
import thunk from 'redux-thunk'
import createHistory from 'history/createBrowserHistory'
import { routerMiddleware } from 'react-router-redux'

import reducer from './reducers/rootReducer'
import productCollection from './productCollection'


const initialState = {


    navigation: {

        pathname: window.location.pathname,

        location: [
            {
                name: 'Продукты',
                link: window.location.pathname
            },
            {
                name: 'Партнеры и Клиенты',
                link: window.location.pathname + 'partners_and_clients'
            },{
                name: 'Торговые марки',
                link: window.location.pathname + 'trade_marks'
            },
            {
                name: 'О нас',
                link:window.location.pathname + 'about'
            },
            {
                name: 'Контакты',
                link: window.location.pathname + 'contacts'
            }
        ],
        classes:{
            headerClassName:'full_screen',
            classLink: 'link_location',
            classButton: '',
        }

    },

    sidebar:{
        h1: 'Продукция',

        categories:{

            p: "Категории",
            items:[
                {
                    name: "Все продукты",
                    id: "allProducts"
                },
                {
                    name: "Тонометры",
                    id: "tonometrs"
                },
                {
                    name: "Небулайзеры",
                    id: "nebulaisers"
                },
                {
                    name: "Ляпко",
                    id: "lyapko"
                },
                {
                    name: "Стетоскопы",
                    id: "stetoskops"
                },
                {
                    name: "Другие товары",
                    id: "other"
                },
            ],

            activeItem: {

                id:"activeItem",
                name: "Все продукты"

            }
        },

        price: {
            h4: "Цены",
            from: '',
            to: ''
        },
        filter:{
            type:"allProducts",
            from:0,
            to:+"Infinity"
        },
        basket: {
            name: "Корзина",
            classes:{
                className:"busket_info",
                classNameEmpty: "empty",
                classNameWith: "with",
                classNameButton: "button_basket",
                classSum:'sum'
            },
            result:{
                quantity:0,
                price:0
            }
        },
        classes: {
            classSideBar:"side_bar",
            classTypesList: "products_types"
        }
    },
    productList:{
        items:productCollection,
        classes:{
            mainWrapper:'item',
            priceClassName:'price_item',
            buttonInBasket: 'in_basket',
            buttonMore: 'more'
        },
    },
    modalWindow:{

        windowOfProduct:{
            opened:false,
            content:"",
            classes:{},
            pageName:'Страница товара'
        },
        windowOfBasket:{
            opened:false,
            content:"",
            classes:{},
            pageName:'Оформление заказа'
        },
        commonClasses:{
            mainWrapper:'modal_page',
            contentPage:'content_page',
            headerContentPage:'header_content_page'
        }


    }
};

export const history = createHistory();

const enhancers = []

const middleware = [
    thunk,
    routerMiddleware(history)
]

if (process.env.NODE_ENV === 'development') {
    const devToolsExtension = window.devToolsExtension

    if (typeof devToolsExtension === 'function') {
        enhancers.push(devToolsExtension())
    }
}

const composedEnhancers = compose(
    applyMiddleware(...middleware),
    ...enhancers
)

const store = createStore(
    reducer,
    initialState,
    composedEnhancers,
);

export default store;


